<script>
import { mapState, mapGetters } from 'vuex'
import CountBox from '@/components/CountBox.vue'
import { Toast } from 'vant'
export default {
  name: 'CartPage',
  data () {
    return {
      isEdit: false
    }
  },
  watch: {
    isEdit (value) {
      if (value) {
        this.$store.commit('cart/toggleAllCheck', false)
      }
    }
  },
  computed: {
    ...mapState('cart', ['cartList']),
    ...mapGetters('cart', ['cartTotal', 'selChecked', 'selCount', 'selPrice', 'isAllChecked']),
    cartLsit () {
      return this.$store.state.cart.cartList
    }
  },
  created () {
    // 请求购物车列表
    if (!this.$store.getters.token) return
    this.$store.dispatch('cart/getCartDataAction')
  },
  methods: {
    toggerCheck (goodsId) {
      this.$store.commit('cart/toggleCheck', goodsId)
    },

    toggleAllCheck () {
      this.$store.commit('cart/toggleAllCheck', !this.isAllChecked)
    },

    changeCount (value, goodsId, skuId) {
      this.$store.dispatch('cart/changeCountAction', {
        value,
        goodsId,
        skuId
      })
    },

    handleDel () {
      this.$store.dispatch('cart/delSelect')
      if (this.$store.dispatch('cart/delSelect')) {
        return Toast('请选择需要删除的商品')
      }
      this.isEdit = false
    },

    // 结算
    goPay () {
      if (this.selChecked.length <= 0) return Toast('请勾选需要购买的商品')
      this.$router.push({
        path: '/pay',
        query: {
          mode: 'cart',
          cartIds: this.selChecked.map(i => i.id).join(',')
        }
      })
    }
  },
  components: {
    CountBox
  }
}
</script>
<template>
  <div class="cart">
    <van-nav-bar title="购物车" fixed />
      <div v-if="cartLsit.length > 0">
        <!-- 购物车开头 -->
        <div class="cart-title">
          <span class="all">共<i>{{ cartTotal || 0 }}</i>件商品</span>
          <span @click="isEdit = !isEdit" class="edit">
            <van-icon name="edit" />
            编辑
          </span>
        </div>

        <!-- 购物车列表 -->
        <div class="cart-list">
          <div class="cart-item" v-for="i in cartList" :key="i.goods_id" >
            <van-checkbox @click="toggerCheck(i.goods_id)" :value="i.isChecked"></van-checkbox>
            <div class="show" @click="$router.push(`/prodetail/${i.goods_id}`)">
              <img :src="i.goods.goods_image">
            </div>
            <div class="info">
              <span class="tit text-ellipsis-2" @click="$router.push(`/prodetail/${i.goods_id}`)">{{ i.goods.goods_name }}</span>
              <span class="bottom">
                <div class="price" @click="$router.push(`/prodetail/${i.goods_id}`)">¥ <span>{{ i.goods.goods_price_min }}</span></div>
                <div class="count-box">
                  <CountBox :value="i.goods_num" @input="value => changeCount(value, i.goods_id, i.goods_sku_id)"></CountBox>
                </div>
              </span>
            </div>
          </div>
        </div>

        <div class="footer-fixed">
          <div  class="all-check" @click="toggleAllCheck">
            <van-checkbox :value="isAllChecked"  icon-size="18"></van-checkbox>
            全选
          </div>

          <div class="all-total">
            <div v-if="!isEdit" class="price">
              <span>合计：</span>
              <span>¥ <i class="totalPrice">{{ selPrice }}</i></span>
            </div>
            <div @click="goPay" v-if="!isEdit" class="goPay">结算({{ selCount }})</div>
            <div @click="handleDel" v-else class="delete">删除({{ selCount }})</div>
          </div>
        </div>
      </div>
    <div class="empty-cart" v-else>
      <img src="@/assets/empty.png" alt="">
      <div class="tips">
      您的购物车是空的, 快去逛逛吧
      </div>
      <div class="btn" @click="$router.push('/')">去逛逛</div>
    </div>
  </div>

</template>

<style lang="less" scoped>
// 主题 padding
.cart {
  padding-top: 46px;
  padding-bottom: 100px;
  background-color: #f5f5f5;
  min-height: 100vh;
  .cart-title {
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    font-size: 14px;
    .all {
      i {
        font-style: normal;
        margin: 0 2px;
        color: #fa2209;
        font-size: 16px;
      }
    }
    .edit {
      .van-icon {
        font-size: 18px;
      }
    }
  }

  .cart-item {
    margin: 0 10px 10px 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    background-color: #ffffff;
    border-radius: 5px;

    .show img {
      width: 100px;
      height: 100px;
    }
    .info {
      width: 210px;
      padding: 10px 5px;
      font-size: 14px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .bottom {
        display: flex;
        justify-content: space-between;
        .price {
          display: flex;
          align-items: flex-end;
          color: #fa2209;
          font-size: 12px;
          span {
            font-size: 16px;
          }
        }
        .count-box {
          display: flex;
          width: 110px;
          .add,
          .minus {
            width: 30px;
            height: 30px;
            outline: none;
            border: none;
          }
          .inp {
            width: 40px;
            height: 30px;
            outline: none;
            border: none;
            background-color: #efefef;
            text-align: center;
            margin: 0 5px;
          }
        }
      }
    }
  }
}

.footer-fixed {
  position: fixed;
  left: 0;
  bottom: 50px;
  height: 50px;
  width: 100%;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;

  .all-check {
    display: flex;
    align-items: center;
    .van-checkbox {
      margin-right: 5px;
    }
  }

  .all-total {
    display: flex;
    line-height: 36px;
    .price {
      font-size: 14px;
      margin-right: 10px;
      .totalPrice {
        color: #fa2209;
        font-size: 18px;
        font-style: normal;
      }
    }

    .goPay, .delete {
      min-width: 100px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      background-color: #fa2f21;
      color: #fff;
      border-radius: 18px;
      &.disabled {
        background-color: #ff9779;
      }
    }
  }

}

.empty-cart {
  padding: 80px 30px;
  img {
    width: 140px;
    height: 92px;
    display: block;
    margin: 0 auto;
  }
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
</style>
